	module.exports = {"data":{"profile":{"id":"374c2819-799e-586c-b922-0f7a96ae2c37","content":"<p><div class=\"content-block  text-center\"><p>Jane Potter, pianist and singer, performs with her jazz groups and as a soloist. She is an Associate Professor at Berklee College of Music in Boston and teaches in the Performance Division. Jane has an extensive background in classical piano and attended her first college, Lewis and Clark College, on a classical piano scholarship. She transferred to Berklee College of Music in Boston and completed her music degree in jazz and contemporary music styles.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>Jane&#8217;s regular jazz gigs in Greater Boston include an eight-year run at the Oak Bar in Copley Square, The Top of the Hub, and Ryles Jazz Club. She has performed live on National Public Radio, at the Democratic Convention, and on WGBH radio. Jane was pictured on the front page of the Boston Sunday Globe (November 2011) and featured in their article on piano bars.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>Jane has worked with vocalists in a variety of genres including Joe McIntyre of the New Kids On the Block; Roland Wyatt, vocal coach for the Manhattan Transfer, and Kamal Scott, an active Broadway performer who had the title role in &#8220;The Wiz&#8221;. She performed for the Aaron Copeland 100th Year Birthday Symposium with internationally known baritone Robert Honeysucker.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>Her versatility as a pianist makes her a good match for clients with virtually any musical taste, be it classical, jazz, pop, r&amp;b, etc&#8230; She has been the resident pianist at C-Zone for the last 10 years, performing at hundreds of private and corporate events throughout New England.</p></div></p>\n","title":"Jane Potter","acf":{"title":"Piano","video":null,"facebook":"","instagram":"","testimonials":""},"image":{"localFile":{"childImageSharp":{"thumbnail":{"base64":"data:image/jpeg;base64,","aspectRatio":0.664,"src":"/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-d0643.jpg","srcSet":"/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-2c2fe.jpg 110w,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-a2e2b.jpg 220w,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-d0643.jpg 440w,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-77eb3.jpg 660w,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-bb5eb.jpg 830w","sizes":"(max-width: 440px) 100vw, 440px","originalImg":"/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-bb5eb.jpg"},"full":{"src":"/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-10d9a.jpg","height":602,"width":400,"srcSet":"/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-10d9a.jpg 1x,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-738fa.jpg 1.5x,\n/static/2ab849268edc880a7d12f7c7c91cfbe6-36a6d472474f6159f19200e7ed72f519-4042a.jpg 2x"}}}},"yoast":{"title":"","canonical":"","metaDescription":"","linkdex":"","metakeywords":"","noIndex":"","noFollow":"","meta_robots_adv":"","metaKeywords":"","redirect":"","ogTitle":"","ogDescription":"","ogImage":"","twitterTitle":"","twitterDescription":"","twitterImage":""}},"site":{"id":"Site","siteMeta":{"title":"C-Zone Entertainment","subtitle":"Premiere DJs & Musicians | Servicing Greater Boston & New England | 617.320.0723"}}},"pathContext":{"id":"374c2819-799e-586c-b922-0f7a96ae2c37"}}

